import React from 'react';
import Header from './header'
import {FilePdf} from 'styled-icons/icomoon/FilePdf';
import {genPromise} from '../plugins';
import ResultView from './result.jsx';

 class PaperItem extends React.Component{
    render(){
        return(
            <li className='paper-item list-group-item'>
                <div className='paper-item-content'>
                    <h6 className='paper-item-title'>{this.props.title}</h6>
                    <p className='paper-item-conf'>{this.props.conf}</p>
                    <small className='paper-item-author'>{this.props.author}</small>
                </div>
                <a className='paper-item-link' href={this.props.link?this.props.link:null}>
                    <FilePdf size='18' />
                    pdf</a>
            </li>
        )
    }
}

export default class Paper extends React.Component{
    constructor(props){
        super(props);
        this.state={
            paper_list:[],
            load:false,
            error:false,
            error_msg:'error'
        }
    }
    componentDidMount(){
        genPromise('GET',"http://39.100.113.162/info/paper/paper.php").then(
            (data)=>{
                if(data.code===0){
                    var paper_list=data.data.map((el)=>{
                        var paper_item_list=el.paper_list.map(sel=>
                            <PaperItem key={sel.id} title={sel.title} conf={sel.conf} author={sel.author}
                                link={sel.pdf_link} />);

                        return(
                            <div>
                                <h4 className='subtitle'>{el.year}</h4>
                                <ul className='list-group'>
                                    {paper_item_list}
                                </ul>
                            </div>
                        )
                    })
                    this.setState({
                        paper_list:paper_list,
                        load:true,
                        error:false
                    })
                }else{
                    this.setState({
                        error:true
                    })
                }
            },(error)=>{
                console.log(error);
                this.setState({
                    error:true,
                    error_msg:error.message
                })
            }
        )
    }
    render(){
        const ContentLoader=()=>(
            <div className='content-loader d-flex justify-content-between'>
                <div className='w-100 p-2'>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'85%',height:'.8rem'}}></div>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'60%',height:'.8rem'}}></div>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'60%',height:'.8rem'}}></div>
                    
                </div>
                <div className='w-50 p-2'>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'85%',height:'.8rem'}}></div>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'75%',height:'.8rem'}}></div>
                    
                </div>
            </div>);
        if(this.state.error){
            return(
                <div><Header />
                <div className='container paper-container'>
                    
                    <ResultView text={this.state.error_msg} />
                </div></div>
            )
        }
        else if(this.state.load){
            return(
                <div> <Header />
                <div className='container paper-container'>
                   
                    <h4 className='title border-bottom'>论文</h4>
                    {this.state.paper_list}
                </div></div>
            )
        }
        else
            return(
                <div><Header />
                <div className='container paper-container'>
                    
                     <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'35%',height:'.8rem'}}></div>
                        <ContentLoader />
                        <ContentLoader />
                        <ContentLoader />
                 </div></div>
                
        )
    }
}